.menu__container {
  padding: 0 40px;
}
.wrapper-menu {
  padding: 8px 0;
  border-bottom: 2px solid transparent;
}

.menu {
  &__list {
    display: flex;
    justify-content: space-between;
    position: relative;
  }

  &__link {
    color: #503737;
    font-size: 17px;
    padding: 12px 15px;
    border-radius: 120px;
    cursor: pointer;
    transition: all 0.4s ease-in-out;
    display: block;
  }
}
li:hover > .menu__link ,
.menu__link:hover {
  background-color: #ff2d2d;
  transition: all 0.3s ease-in-out;
  color: #fefefe;
}

.wrapper-menu:hover {
  border-bottom: 2px solid #ff2d2d;
}

.menu__link--act {
  color: #c1a25a;
  font-weight: 700;
}
.submenu {
  display: none;
  position: absolute;
  width: 250px;
  padding-top: 10px;
}
.sub-submenu {
  position: absolute;
  left: 100%;
}
.menu__list li:hover .submenu {
  display: block;
}
.submenu__link {
  transition: all 0.3s ease-in-out;

  a {
    display: block;
    padding: 11px 25px;


    &:hover {
      background-color: #af9559;
      transition: all 0.3s ease-in-out;
    }
  }
  color: #ffffff;
  background-color: rgba(#333, 0.8);
  font-size: 18px;
  cursor: pointer;
}
.top {
  padding-top: 15px;
}
.sub-top {
  padding-top: 10px;
}
.sub-submenu {
  display: none;
  top: 0;
  width: 100%;
  padding-top: 10px;
}
.submenu li:hover > .sub-submenu {
  display: block;
  top: 0;
}
.sub-submenu__link {
  background-color: #af9559;

  a {
    transition: all 0.3s ease-in-out;
    &:hover {
      background-color: rgba(#000, 0.7);
      transition: all 0.3s ease-in-out;
    }
  }
}
