@import url(./vendor/null.css);
:root {
  --color-black: #000;
  --color-white: #fff;
}

@font-face {
  font-family: "MinionPro";
  src: url("../fonts/MinionPro-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Tahoma";
  src: url("../fonts/Tahoma-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Tahoma";
  src: url("../fonts/Tahoma-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

