h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Tahoma", sans-serif;
  font-weight: 400;
  margin: 0;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1030px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header__container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 970px;
}

.header__wrapper {
  background-color: #af9559;
}

.header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;
}

.header__top-adress {
  font-size: 14px;
  color: #fff;
}

.header__top-tel {
  position: relative;
  display: inline-block;
  font-size: 14px;
  color: #fefefe;
  padding-right: 44px;
  padding-left: 24px;
}

.header__top-tel:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  background-image: url("../img/tel.svg");
  width: 17px;
  height: 18px;
  color: #fefefe;
}

.header__top-list {
  display: flex;
  align-items: center;
}

.header__top-link {
  color: #fefefe;
  font-size: 14px;
  font-weight: 400;
}

.header__top-link a {
  border-bottom: 1px solid #fefefe;
}

.header__top-link + .header__top-link {
  padding-left: 17px;
}

.header__top-link--ars {
  margin-left: 25px;
}

.header__top-link--ars a {
  border-bottom: 1px dashed #fefefe;
}

.header__top-link--vk {
  background-image: url("../img/vk.png");
  background-repeat: no-repeat;
  background-position: center;
  width: 22px;
  height: 22px;
  margin-left: 18px;
}

.search__bottom {
  border-bottom: 1px solid #919191;
}

.search__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 20px;
  padding-bottom: 27px;
}

.search__logo {
  text-align: center;
}

.search__text {
  color: #777777;
  font-size: 14px;
  margin-top: 8px;
}

.search__form {
  width: 380px;
  height: 40px;
  border: 1px solid #919191;
  border-radius: 3px;
  overflow: hidden;
  position: relative;
  margin-top: 5px;
}

.search__inp {
  width: 100%;
  height: 100%;
  padding-left: 14px;
  padding-right: 45px;
}

.search__form-btn {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  background-image: url("../img/search.svg");
  right: 14px;
  top: 50%;
  transform: translateY(-50%);
}

.search__cart {
  width: 150px;
  height: 40px;
  color: #444444;
  font-size: 14px;
  border: 1px solid #919191;
  background-color: #f1f1f1;
  border-radius: 3px;
  position: relative;
  margin-top: 5px;
}

.search__cart::before {
  content: "";
  position: absolute;
  background-image: url("../../img/cart.svg");
  width: 25px;
  height: 20px;
  top: 50%;
  left: 13px;
  transform: translateY(-50%);
}

.menu__container {
  padding: 0 40px;
}

.wrapper-menu {
  padding: 8px 0;
  border-bottom: 2px solid transparent;
}

.menu__list {
  display: flex;
  justify-content: space-between;
  position: relative;
}

.menu__link {
  color: #503737;
  font-size: 17px;
  padding: 12px 15px;
  border-radius: 120px;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
  display: block;
}

li:hover > .menu__link,
.menu__link:hover {
  background-color: #ff2d2d;
  transition: all 0.3s ease-in-out;
  color: #fefefe;
}

.wrapper-menu:hover {
  border-bottom: 2px solid #ff2d2d;
}

.menu__link--act {
  color: #c1a25a;
  font-weight: 700;
}

.submenu {
  display: none;
  position: absolute;
  width: 250px;
  padding-top: 10px;
}

.sub-submenu {
  position: absolute;
  left: 100%;
}

.menu__list li:hover .submenu {
  display: block;
}

.submenu__link {
  transition: all 0.3s ease-in-out;
  color: #ffffff;
  background-color: rgba(51, 51, 51, 0.8);
  font-size: 18px;
  cursor: pointer;
}

.submenu__link a {
  display: block;
  padding: 11px 25px;
}

.submenu__link a:hover {
  background-color: #af9559;
  transition: all 0.3s ease-in-out;
}

.top {
  padding-top: 15px;
}

.sub-top {
  padding-top: 10px;
}

.sub-submenu {
  display: none;
  top: 0;
  width: 100%;
  padding-top: 10px;
}

.submenu li:hover > .sub-submenu {
  display: block;
  top: 0;
}

.sub-submenu__link {
  background-color: #af9559;
}

.sub-submenu__link a {
  transition: all 0.3s ease-in-out;
}

.sub-submenu__link a:hover {
  background-color: rgba(0, 0, 0, 0.7);
  transition: all 0.3s ease-in-out;
}

.intro {
  background-image: url("../img/header-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 500px;
}
