.header {
  &__container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 970px;
  }
  &__wrapper {
    background-color: #af9559;
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 50px;
  }

  &__top-adress {
    font-size: 14px;
    color: #fff;
  }

  &__top-tel {
    position: relative;
    display: inline-block;
    font-size: 14px;
    color: #fefefe;
    padding-right: 44px;
    padding-left: 24px;

    &:after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      background-image: url("../img/tel.svg");
      width: 17px;
      height: 18px;
      color: #fefefe;
    }
  }

  &__top-list {
    display: flex;
    align-items: center;
  }

  &__top-link {
    color: #fefefe;
    font-size: 14px;
    font-weight: 400;
    a {
      border-bottom: 1px solid #fefefe;
    }
    & + & {
      padding-left: 17px;
    }
  }

  &__top-link--ars {
    margin-left: 25px;
    a {
      border-bottom: 1px dashed #fefefe;
    }
  }

  &__top-link--vk {
    background-image: url("../img/vk.png");
    background-repeat: no-repeat;
    background-position: center;
    width: 22px;
    height: 22px;
    margin-left: 18px;
  }
}

.search {
  &__bottom {
    border-bottom: 1px solid #919191;
  }
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 20px;
    padding-bottom: 27px;
  }

  &__logo {
    text-align: center;
  }

  &__text {
    color: #777777;
    font-size: 14px;
    margin-top: 8px;
  }

  &__form {
    width: 380px;
    height: 40px;
    border: 1px solid #919191;
    border-radius: 3px;
    overflow: hidden;
    position: relative;
    margin-top: 5px;
  }

  &__inp {
    width: 100%;
    height: 100%;
    padding-left: 14px;
    padding-right: 45px;
  }

  &__form-btn {
    position: absolute;
    content: "";
    width: 20px;
    height: 20px;
    background-image: url("../img/search.svg");
    right: 14px;
    top: 50%;
    transform: translateY(-50%);
  }

  &__cart {
    width: 150px;
    height: 40px;
    color: #444444;
    font-size: 14px;
    border: 1px solid #919191;
    background-color: #f1f1f1;
    border-radius: 3px;
    position: relative;
    margin-top: 5px;

    &::before {
      content: "";
      position: absolute;
      background-image: url("../../img/cart.svg");
      width: 25px;
      height: 20px;
      top: 50%;
      left: 13px;
      transform: translateY(-50%);
    }
  }
}



